<style scoped>
.homepage {
    display: flex;
    flex-direction: row;
}
.brand {
    width: 50px;
    height: 50px;
}
.brand-title {
    margin-left: 10px;
    font-size: 26px;
}
.layout{
    border: 1px solid #d7dde4;
    background: #f5f7f9;
}
.layout-logo{
    float: left;
    position: relative;
    top: 5px;
    left: 40px;
}
.layout-nav{
    width: 520px;
    margin: 0 auto;
}


.layout-content{
    min-height: 200px;
    margin: 15px;
    overflow: hidden;
    background: #fff;
    border-radius: 4px;
}

.layout-copy{
    text-align: center;
    padding: 10px 0 20px;
    color: #9ea7b4;
}
.layout-content {
    padding: 50px;
}
</style>
<template>
    <div class="layout">
        <Menu mode="horizontal" theme="light" active-name="1" v-on:on-select="routeChange">
            <div class="layout-logo">
                <a href="/" class="homepage">
                    <img src="../assets/logo.png" alt="brand" class="brand">
                    <span class="brand-title">Daisy（雏菊）</span>
                </a>
            </div>
            <div class="layout-nav">
                <Menu-item :name="nav.name" v-for="(nav,index) in nav_lev1" :key="index">
                    <Icon :type="nav.icon"></Icon>
                    {{nav.title}}
                </Menu-item>
            </div>
        </Menu>

        <div class="layout-content">
            <router-view></router-view>
        </div>
        <div class="layout-copy">Angela &copy; 2017. 保留所有权利。</div>
    </div>
</template>
<script>
'use strict'

export default {
  data () {
    return {
      nav_lev1: [{
        icon: 'android-apps',
        title: '内务管理',
        name: 'daily'
      }, {
        icon: 'gear-a',
        title: '班级宿舍管理',
        name: 'class'
      }, {
        icon: 'information',
        title: '关于',
        name: 'about'
      }]
    }
  },
  methods: {
    routeChange: function (value) {
      this.$router.push('/' + value)
    }
  }

}
</script>



